.App {
  text-align: center;
}
.question-card{
  background-color: grey;
  padding: 16px;
  width: 80%;
  height: auto;
  margin: 0 auto;
  border-radius: 16px;
  color: #fff;
  box-shadow: rgba(50, 50, 93, 0.25) 0px 30px 60px -12px,
    rgba(0, 0, 0, 0.3) 0px 18px 36px -18px;
}
ul{
  list-style-type: none;
  padding: 0;
  
}
li{
  padding: 0;
  background-color: darkgrey;
  padding: 16px;
  border: 3px solid white;
  border-radius: 20px;
  font-size: 20px;
  margin-top: 8px;
  cursor: pointer;
}

h3{
  color: darkblue;
  font-size: 24px;
}

.final-results{
  background-color: grey;
  padding: 16px;
  width: 50%;
  height: auto;
  margin: 0 auto;
  border-radius: 16px;
  color: #fff;
  box-shadow: rgba(50, 50, 93, 0.25) 0px 30px 60px -12px,
    rgba(0, 0, 0, 0.3) 0px 18px 36px -18px;
}
button{
  background-color: red; /* Green */
  border: none;
  color: white;
  padding: 16px 24px;
  text-align: center;
  text-decoration: none;
  display: inline-block;
  font-size: 16px;
  font-weight: bold;
  border-radius: 24px;
}